<template>
    <div class="format-param">
        <div class="ft-14">JSONJSON</div>

        <el-input v-model="textarea1" type="textarea" :rows="4" placeholder=""> </el-input>

        <el-button-group>
            <el-button type="primary" icon="el-icon-refresh" @click="conversion"></el-button>
            <el-button type="primary" icon="el-icon-copy-document" @click="copy"></el-button>
            <el-button type="danger" icon="el-icon-delete" @click="reset"></el-button>
        </el-button-group>

        <el-input v-model="textarea2" type="textarea" :rows="4"> </el-input>
    </div>
</template>

<script>
import { clipboard, parsedeep } from '@/utils/util';
export default {
    data() {
        return {
            textarea1: '',
            textarea2: '',
        };
    },
    methods: {
        reset() {
            this.textarea1 = '';
            this.textarea2 = '';
        },
        conversion() {
            console.log('this.textarea1:@#@', this.textarea1, typeof this.textarea1);
            let newValue = this.textarea1.trim();
            newValue = JSON.parse(newValue);
            console.log('parsedeep', parsedeep(newValue));
            const paStr = parsedeep(newValue);
            this.textarea2 = JSON.stringify(paStr);
        },
        copy() {
            const value = this.textarea2;
            if (value) {
                clipboard(value);
                this.$message.success('');
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.format-param {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
}
</style>
