/**
 * 
 * @param {*} text
 */
export function clipboard(text) {
    console.log('', text);
    if (navigator.clipboard) {
        navigator.clipboard.writeText(text);
    } else {
        let textarea = document.createElement('textarea');
        document.body.appendChild(textarea);
        // 
        textarea.style.position = 'fixed';
        textarea.style.clip = 'rect(0 0 0 0)';
        textarea.style.top = '10px';
        // 
        textarea.value = text;
        // 
        textarea.select();
        // 
        document.execCommand('copy', true);
        // 
        document.body.removeChild(textarea);
    }
}

/**
 * 
 * @param {string} format 
 * @returns {string}
 */
export const getDateTime = (format = 'YYYY-MM-DD HH:mm:ss') => {
    const currentDate = new Date();
    const placeholders = {
        YYYY: currentDate.getFullYear(),
        MM: String(currentDate.getMonth() + 1).padStart(2, '0'),
        DD: String(currentDate.getDate()).padStart(2, '0'),
        HH: String(currentDate.getHours()).padStart(2, '0'),
        mm: String(currentDate.getMinutes()).padStart(2, '0'),
        ss: String(currentDate.getSeconds()).padStart(2, '0'),
    };

    const formattedDateTime = format.replace(/YYYY|MM|DD|HH|mm|ss/g, match => placeholders[match]);
    return formattedDateTime;
};

export const isJson = str => {
    if (typeof str === 'string') {
        if (!/(^{.*}$)|(^".*"$)|(^\[.*\]$)/.test(str)) {
            return false;
        }
        try {
            JSON.parse(str);
            return true;
        } catch (e) {
            return false;
        }
    } else {
        return false;
    }
};

/**
 * 
 * @param {Object|Array} source - 
 * @returns {Object|Array} - 
 */
export const parsedeep = source => {
    // 
    if (source === null || typeof source !== 'object') {
        if (typeof source === 'string' && isJson(source)) {
            source = JSON.parse(source);
        } else {
            return source;
        }
    }
    const target = Array.isArray(source) ? [] : {};
    const keys = Object.keys(source);
    keys.forEach(key => {
        target[key] = parsedeep(source[key]);
    });
    return target;
};
