/**
 * unicode16
 * @param {*} str
 * @returns
 */
export function unicodeToHex(str) {
    if (str === '') return '';
    var hexCharCode = [];
    for (var i = 0; i < str.length; i++) {
        let hexCode = str.charCodeAt(i).toString(16);
        hexCharCode.push(padWithZeros(hexCode, 4));
    }
    return hexCharCode.join('');
}

/**
 * 16unicode
 * @param {*} str
 * @returns
 */
export function hexToUnicode(str) {
    if (!str || str.length % 4 != 0) {
        return '';
    }
    let charList = [];
    let chunks = arrayChunk(str, 4);
    for (let index = 0; index < chunks.length; index++) {
        const element = chunks[index];
        charList.push(String.fromCharCode(parseInt(element, 16)));
    }
    return charList.join('');
}

/**
 * 
 * @param {Object} array
 * @param {Object} chunkSize
 */
export function arrayChunk(array, chunkSize) {
    let chunks = [];
    for (let i = 0; i < array.length; i += chunkSize) {
        const chunk = array.slice(i, i + chunkSize);
        chunks.push(chunk);
    }
    return chunks;
}

export function padWithZeros(num, size) {
    var s = num + '';
    while (s.length < size) {
        s = '0' + s;
    }
    return s;
}
