<template>
    <div class="format-param">
        <div class="ft-14"></div>

        <el-input v-model="textarea1" type="textarea" :rows="4" placeholder=""> </el-input>

        <el-button-group>
            <el-button type="primary" icon="el-icon-refresh" @click="format"></el-button>
            <el-button type="primary" icon="el-icon-refresh" @click="compress"></el-button>
            <el-button type="primary" icon="el-icon-copy-document" @click="copy"></el-button>
            <el-button type="danger" icon="el-icon-delete" @click="reset"></el-button>
        </el-button-group>

        <pre class="pre">{{ textarea2 }}</pre>
    </div>
</template>

<script>
import { clipboard } from '@/utils/util';
export default {
    data() {
        return {
            textarea1: '',
            textarea2: '',
        };
    },
    methods: {
        reset() {
            this.textarea1 = '';
            this.textarea2 = '';
        },
        format() {
            let obj = JSON.parse(this.textarea1);
            this.textarea2 = JSON.stringify(obj, null, 8);
        },
        compress() {
            let obj = JSON.parse(this.textarea1);
            this.textarea2 = JSON.stringify(obj, null, 0);
        },
        copy() {
            const value = this.textarea2;
            if (value) {
                clipboard(value);
                this.$message.success('');
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.format-param {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
}

.pre {
    border: 1px solid #009280;
    white-space: break-spaces;
    font-size: 18px;
}
</style>
