<template>
    <div class="encode-decode">
        <div class="ft-14">BASE64</div>

        <el-input v-model="textarea1" type="textarea" :rows="4" placeholder=""> </el-input>
        <el-button-group>
            <el-button type="primary" icon="el-icon-refresh" @click="encode"></el-button>
            <el-button type="primary" icon="el-icon-refresh" @click="decode"></el-button>
            <el-button type="primary" icon="el-icon-copy-document" @click="copy"></el-button>
            <el-button type="danger" icon="el-icon-delete" @click="reset"></el-button>
        </el-button-group>
        <el-input v-model="textarea2" type="textarea" :rows="4" readonly> </el-input>
    </div>
</template>

<script>
import { clipboard } from '@/utils/util';

/**
 * Base64 
 */
const Base64Util = {
    /**
     * Base64 
     * @param {string} str - 
     * @returns {string} - Base64 
     */
    encode(str) {
        if (typeof window !== 'undefined' && window.btoa) {
            // 
            return window.btoa(unescape(encodeURIComponent(str)));
        } else if (typeof Buffer !== 'undefined') {
            // Node.js 
            return Buffer.from(str, 'utf-8').toString('base64');
        } else {
            throw new Error(' Base64 ');
        }
    },

    /**
     * Base64 
     * @param {string} base64 - Base64 
     * @returns {string} - 
     */
    decode(base64) {
        if (typeof window !== 'undefined' && window.atob) {
            // 
            return decodeURIComponent(escape(window.atob(base64)));
        } else if (typeof Buffer !== 'undefined') {
            // Node.js 
            return Buffer.from(base64, 'base64').toString('utf-8');
        } else {
            throw new Error(' Base64 ');
        }
    },
};
export default {
    data() {
        return {
            textarea1: '',
            textarea2: '',
        };
    },
    methods: {
        encode() {
            if (this.textarea1) {
                this.textarea2 = Base64Util.encode(this.textarea1);
            }
        },
        decode() {
            if (this.textarea1) {
                this.textarea2 = Base64Util.decode(this.textarea1);
            }
        },
        copy() {
            const value = this.textarea2;
            if (value) {
                clipboard(value);
                this.$message.success('');
            }
        },
        reset() {
            this.textarea1 = '';
            this.textarea2 = '';
        },
    },
};
</script>

<style lang="scss" scoped>
.encode-decode {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
</style>
