<template>
    <div class="hex-coding">
        <div class="ft-14">16</div>

        <el-input v-model="textarea1" type="textarea" :rows="4" placeholder=""> </el-input>

        <el-button-group>
            <el-button type="primary" icon="el-icon-refresh" @click="encodingClick"></el-button>
            <el-button type="primary" icon="el-icon-refresh" @click="decodingClick"></el-button>
            <el-button type="primary" icon="el-icon-copy-document" @click="copy"></el-button>
            <el-button type="danger" icon="el-icon-delete" @click="reset"></el-button>
        </el-button-group>

        <el-input v-model="textarea2" type="textarea" :rows="4" readonly> </el-input>
    </div>
</template>

<script>
import { clipboard } from '@/utils/util';
import { unicodeToHex, hexToUnicode } from '@/utils/data-conversion';
export default {
    data() {
        return {
            textarea1: '',
            textarea2: '',
        };
    },
    methods: {
        reset() {
            this.textarea1 = '';
            this.textarea2 = '';
        },
        copy() {
            const value = this.textarea2;
            if (value) {
                clipboard(value);
                this.$message.success('');
            }
        },
        encodingClick() {
            let value = this.textarea1;
            if (value) {
                this.textarea2 = unicodeToHex(value);
            }
        },
        decodingClick() {
            let value = this.textarea1;
            if (value) {
                this.textarea2 = hexToUnicode(value);
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.hex-coding {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
}
</style>
