<template>
    <div class="encode-decode">
        <div class="ft-14">encodeURIComponent decodeURIComponent</div>

        <el-input v-model="textarea1" type="textarea" :rows="4" placeholder=""> </el-input>
        <el-button-group>
            <el-button type="primary" icon="el-icon-refresh" @click="encode"></el-button>
            <el-button type="primary" icon="el-icon-refresh" @click="decode"></el-button>
            <el-button type="primary" icon="el-icon-copy-document" @click="copy"></el-button>
            <el-button type="danger" icon="el-icon-delete" @click="reset"></el-button>
        </el-button-group>
        <el-input v-model="textarea2" type="textarea" :rows="4" readonly> </el-input>
    </div>
</template>

<script>
import { clipboard } from '@/utils/util';
export default {
    data() {
        return {
            textarea1: '',
            textarea2: '',
        };
    },
    methods: {
        encode() {
            if (this.textarea1) {
                this.textarea2 = encodeURIComponent(this.textarea1);
            }
        },
        decode() {
            if (this.textarea1) {
                this.textarea2 = decodeURIComponent(this.textarea1);
            }
        },
        copy() {
            const value = this.textarea2;
            if (value) {
                clipboard(value);
                this.$message.success('');
            }
        },
        reset() {
            this.textarea1 = '';
            this.textarea2 = '';
        },
    },
};
</script>

<style lang="scss" scoped>
.encode-decode {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
</style>
