<template>
    <div class="hex-coding">
        <div class="ft-14">Base64  + zlib /</div>
        <div>window.unzip('').then(res => console.log('', res))</div>

        <el-input v-model="textarea1" placeholder="" style="white-space: nowrap"></el-input>

        <el-button-group style="margin-top: 57px">
            <el-button type="primary" icon="el-icon-refresh" @click="handleUnzip"></el-button>
            <el-button type="primary" icon="el-icon-copy-document" @click="copy"></el-button>
        </el-button-group>

        <el-input v-model="textarea2" type="textarea" :rows="4" readonly></el-input>
    </div>
</template>

<script>
import pako from 'pako';
import { clipboard } from '@/utils/util';

function largeuint8ArrToString(uint8arr) {
    return new Promise(async resolve => {
        var bb = new Blob([uint8arr]);
        var f = new FileReader();
        f.onload = function (e) {
            resolve(e.target.result);
        };
        f.readAsText(bb);
    });
}

// 
async function unzip(b64Data) {
    let strData = atob(b64Data);
    // Convert binary string to character-number array
    const charData = strData.split('').map(function (x) {
        return x.charCodeAt(0);
    });
    // Turn number array into byte-array
    const binData = new Uint8Array(charData);
    // // unzip
    const data = pako.inflate(binData);
    let unzipRes = await largeuint8ArrToString(data);
    return unzipRes;
    // Convert gunzipped byteArray back to ascii string:
    // strData = String.fromCharCode.apply(null, new Uint16Array(data))
    // strData = this.handleCodePoints(new Uint16Array(data))
    // this.c = strData
}

export default {
    data() {
        return {
            textarea1: '',
            textarea2: '',
        };
    },
    mounted() {
        window.unzip = unzip;
    },
    methods: {
        async handleUnzip() {
            let value = this.textarea1;
            if (value) {
                let result = await unzip(value);
                this.textarea2 = result;
            }
        },
        copy() {
            const value = this.textarea2;
            if (value) {
                clipboard(value);
                this.$message.success('');
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.hex-coding {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
}
</style>
